import React, {Component} from 'react';
import './App.css';
import Footer from "./components/Footer";
import * as mock from './mocks/booksAppMock';
import BookInfo from "./components/BookInfo";


class App extends Component {

    constructor() {
        super();

        this.state = {
            showBookInfo: false,
            bookIndex: 0,
        };

        this.showBookInfo = this.showBookInfo.bind(this);
    }


    showBookInfo(evt, data, index) {
        this.setState({
            showBookInfo: true,
            bookIndex: index,
        })
    }

    render() {
        let grid = [];
        for (let i = 0; i < 15; i++) {
            grid.push(<li key={"grid-" + i} className={"grid-item"}
                          onClick={e => this.showBookInfo(e, mock.data[i], i)}
            >
                {mock.data[i].title}
            </li>);
        }
        return (
            <div className={"App-bg"}>
                <div className={"App"}>
                    <ul className={"books-list"}>
                        {grid}
                    </ul>
                    {this.state.showBookInfo ?
                        <BookInfo bookData={mock.data[this.state.bookIndex]}/>
                        : null
                    }
                </div>
                    <Footer/>
            </div>

        );
    }


}

export default App;



