import React, {Component} from 'react';

class Footer extends Component {
    render() {

        return (
            <div>
                <div className="footer">
                    @Copyrights by Omri Ratson - {new Date().getFullYear()}
                </div>
            </div>
        );
    }

}

export default Footer;


